.donationContent{margin-top:100px}.donationContent .content{max-width:500px;width:100%;margin:0 auto;padding:20px;margin-bottom:60px;background-color:#f8f9fa;border-radius:8px;box-shadow:0 2px 4px rgba(0,0,0,.1)}.donationContent .content h2{color:#333;text-align:center;margin-bottom:20px}.donationContent .content .formGroup{margin-bottom:20px}.donationContent .content .formGroup label{display:block;margin-bottom:5px;color:#555}.donationContent .content .formGroup input[type=number],.donationContent .content .formGroup input[type=file],.donationContent .content .formGroup textarea{width:100%;padding:10px;border:1px solid #ddd;border-radius:4px;font-size:16px}.donationContent .content .formGroup textarea{height:100px;resize:vertical}.donationContent .content .submitButton{display:block;width:100%;padding:10px;background-color:#007bff;color:#fff;border:none;border-radius:4px;font-size:16px;cursor:pointer;transition:background-color .3s}.donationContent .content .submitButton:hover{background-color:#0056b3}.donationContent .content .error{color:#dc3545;font-size:14px;margin-top:5px;display:block}